import React from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider, Paragraph,createTheme, Project, Logo, Image, Header, Arwes, Words, SoundsProvider, createSounds, Button } from 'arwes';

const sounds = {
  shared: { volume: 1, },
  players: {
    click: {
      sound: { src: ['/static/sound/click.mp3'] }
    },
    typing: {
      sound: { src: ['/static/sound/typing.mp3'] },
      settings: { oneAtATime: true }
    },
    deploy: {
      sound: { src: ['/static/sound/deploy.mp3'] },
      settings: { oneAtATime: true }
    },
    information: { sound: { src: ['/static/sound/information.mp3'] } },
    ask: { sound: { src: ['/static/sound/ask.mp3'] } },
    warning: { sound: { src: ['/static/sound/warning.mp3'] } },
    error: { sound: { src: ['/static/sound/error.mp3'] } },
  }
};

async function postData(url = '', data = {}) {
  const response = await fetch(url, {
    method: 'POST',
    mode: 'cors',
    cache: 'no-cache',
    credentials: 'same-origin',
    headers: {
      'Content-Type': 'application/json'
    },
    redirect: 'follow',
    referrerPolicy: 'no-referrer',
    body: JSON.stringify(data)
  });
  return response.json();
}

const handleSubmit = (event) => {
    postData('/backend/check_code', { code: document.getElementById("code").value })
  .then(data => {
    var images = document.getElementsByTagName('IMG');
    for (var i = 0; i < images.length; ++i) {
        if (images[i].src.includes("flicker.gif")){
            images[i].src = "/backend/static/flicker.gif?random="+new Date().getTime();
        }
    }
    document.getElementById("resp").innerHTML = data.resp;
  });
    event.preventDefault();
}


const App = () => {
    return (
    <ThemeProvider theme={createTheme()}>
    <SoundsProvider sounds={createSounds(sounds)}>
    <Arwes animate background='/static/img/background.jpg' pattern='/static/img/glow.png'>
        {anim => (
        <div style={{ margin: 20 }}>
            <Header animate>
                <h1 style={{ margin: 0 }}><Logo animate /> Next Generation Payout</h1>
            </Header>
        <br/>
        <Project animate header='Welcome to your next generation payout service'>
        <Paragraph><Words animate show={anim.entered}>
            Just scan the following code with our banking app, enter the security code and recieve your payment.
            </Words>
        </Paragraph>
        <br/>
        <div style={{margin: '0 auto', maxWidth: 350 }}>
        <Image id="generatedCode" style={{width:'350px'}} animate show={anim.entered} resources='/backend/static/flicker.gif'>
          Scan here
        </Image>
        <form onSubmit={handleSubmit}>
             <label>Security Code: </label>
             <input type="text" id="code" name="code"></input>
             <Button animate show={anim.entered}>Verify</Button>
        </form>
        <br/>
        <Words animate id="resp">
        </Words>
        </div>
        </Project>
        </div>
        )}
    </Arwes>
    </SoundsProvider>
    </ThemeProvider>
    )
};

ReactDOM.render(<App />, document.querySelector('#root'));
